@use '../abstracts' as *;
@use '../base' as *;

// Desktop-specific layout
main > article {
    .banner-news {
        .banner__image {
            grid-column: 1 / span 24;
        }

        .banner-news__label {
            grid-column: 3 / 23;
        }
    }
}

// Generic styles
.banner-news {
    @include x-column-grid(24);
    display: grid;

    .banner__image {
        overflow: hidden;
        max-height: 400px;
    }

    .banner__block-color {
        background-color: $color-black;
        grid-column: 1 / -1;
        height: $measure--huge1;

        @include media('<tablet') {
            height: $measure--large1;
        }
    }

    .banner-news__label {
        background: $color-black;
        margin-top: 0;
        grid-column: 1 / -1;
        text-align: center;
        padding: 2rem;

        @include media('>=tablet') {
            margin-top: -$measure--medium9;
            grid-column: 2 / 24;
        }

        @include media('>small') {
            grid-column: 2 / 18;
        }

        @include media('>desktop') {
            grid-column: 3 / 17;
        }
    }


.banner__image {
    height: 100%;
    // always on first row
    grid-row: 1;

    @include media('<=tablet') {
        max-height: $banner-mobile-image-max-height;
    }

    @include media('>tablet') {
        max-height: $banner-tablet-image-max-height;
    }

    @include media('>=desktop') {
        max-height: $banner-desktop-image-max-height;
    }

    @include media('>=4K') {
        max-height: $banner-4k-image-max-height;
    }

    @include media('>=8K') {
        max-height: $banner-8k-image-max-height;
    }

    .hero-image {
        width: 100%;
        overflow: hidden;
        display: flex;

        img {
            width: 100%;
            object-fit: cover;
            align-self: auto;
        }
    }
}

    .labels {
        @include smalltype();

        color: $color-text--sub;
        font-weight: $font-weight-bold;
        margin-bottom: $measure--half;
        margin-left: auto;
        margin-right: auto;
        text-align: center;
    }
}
