@use '../abstracts' as *;

.listing {
    display: grid;
    grid-template-columns: 1fr;
    gap:$measure--basic;

    @include media('>=tablet') {
        grid-template-columns: repeat(2, 1fr);
    }
    @include media('>=small') {
        grid-template-columns: repeat(3, 1fr);
    }



    &--membership {
        gap:$measure--medium1 $measure--medium2;
    }
}


