@use '../../abstracts' as *;
@use '../../base' as *;
// @use '../../layout' as *;


.membership__price {
    color: $color-purewhite;
    font-size: $font-size--zeta;
    line-height:  $line-height-ratio--heading;
    margin-top: $measure--half;
}
.membership__price-term {
    display: block;
    font-size:$font-size--smalltype;
    

    //$font-size--smalltype
}

.membership__benefits {
    color:$color-simple-grey;
    display: flex;
    flex-direction: column;
    list-style: none;
    margin:0;
    li {
        margin: 0;
        display: flex;

        &:not(.membership__benefits--no-tick){
            &:before {
                color:$color-purewhite;
                margin-top:-1px;
                content: "\f058";
                font-weight: 400;
                font-family: 'Font Awesome 6 Pro';
                margin-right:$measure--half;
                font-size: 1.2em;
            }
        }

        &.membership__benefits--no-tick{
            color: $color-mid-grey;
        }

    }
}