@use "../../abstracts" as *;
@use "../../base" as *;

.slanted-txt-bg {
    text-transform: uppercase;
    font-style: italic;
    font-weight: $font-weight-bold;
    @include thetatype();
    
    display: flex;
    flex-direction: column;

    @include media('>=tablet') {
        font-size: $measure--medium3;
    }



   &[data-count='2']{
        flex-direction: row;

        .slanted-line {

            @include media('>=tablet') {
                &:first-child {
                    padding-right: 0.4rem;
                }
                &:last-child {
                    padding-left: 0;
                }
            }
        }

        @include media('<tablet') {
            flex-direction: column;
        }
    }

    .slanted-line {
        color: $color-purewhite;
        position: relative;
        display: inline-block;
        padding: 0rem 0.6rem;
        margin-bottom: -0.1rem;
        z-index: z-index("default");
        width: fit-content;
        
    
        &:before {
            content: "";
            position: absolute;
            top: 0;
            left: -5px;
            right: -5px;
            bottom: 0;
            background: $color-sport-red;
            transform: skewX(-20deg);
            z-index: z-index("below");
            border-radius: 2px;
        }
    }
}


.content-announcement__wrapper {
    display: inline-block;
}

.content-announcement {
    border: $border-width-hairline solid $color-gray7;
    padding: $measure--half;
    display: flex;
    border-radius: $measure--half;
}

.content-announcement__icon,
.content-announcement__text {
    align-self: center;
}

.content-announcement__icon {
    padding-right: $measure--half;
    @include zetatype();
}

.content-announcement__text {
    p {
        margin: 0;
        padding: 0;
    }
}
