@use '../../abstracts' as *;
@use '../../base' as *;
@use '../../layout' as *;

.footer-oh__wrap {
    background: linear-gradient(270deg, $color-gray5 0%, $color-red50 50%, $color-red50 100%)
}


.footer-oh {
    padding-top: $measure--medium6;
    padding-bottom: $measure--medium6;

    
    
    @include standard-box();
    @include media('>=small') {
        
        @include x-column-grid(24);
    }
}

.footer-oh__heading {
    text-align: center;
    margin:0;
    @include media('>=tablet') {
        text-align: left;
    }
}
.footer-oh__content {
    margin-top: $measure--small2;
    display: flex;
    flex-direction: column;
    gap:$measure--basic;
    text-align: center;
    @include media('>=tablet') {
        text-align: left;
        flex-direction: row;
    }
}

.footer-oh__col {
    flex-basis: 25%;
    display: flex;
    flex-direction: column;

    span {
        margin-bottom: $measure--half;
    }
}

.footer-oh__col--cta {
    display: flex;
    align-items: center;
    justify-content: center;
    @include media('>=tablet') {
        align-items: self-end;
    }
}