////
/// Reset and foundation mixins
///
/// Provide a baseline for anchor styles, to avoid multiple anchor styles
/// clashing in deeply nested content.
///
/// @group abstracts
////

@use 'variables' as *;
@use 'measurements' as *;
@use 'colors' as *;
@use 'palette' as *;
@use 'breakpoints' as *;


@mixin anchor-reset-properties() {
    background-color: initial;
    background-image: initial;
    background-size: initial;
    border-bottom-color: initial;
    border-bottom-style: none;
    border-bottom-width: 0;
    color: initial;
    cursor: pointer;
    outline-color: initial;
    outline-offset: 0;
    outline-style: none;
    outline-width: 0;
    text-decoration: none;
}

@mixin anchor-reset() {
    @include anchor-reset-properties();

    &:hover {
        @include media('hover') {
            @include anchor-reset-properties();
        }
    }

    &:focus,
    &:active {
        @include anchor-reset-properties();
    }
}

@mixin anchor-border-reset() {
    border: none;
    border-bottom-width: 0;
    border-bottom-color: transparent;
    border-bottom-style: none;
}

@mixin outline-properties($color: $color-outline) {
    outline-color: $color;
    outline-offset: $border-width-thick;
    outline-style: $border-style-solid;
    outline-width: $border-width-default;
}

@mixin anchor-default-outline($color: $color-outline) {
    &:focus,
    &:active {
        @include outline-properties($color);
    }
}

@mixin zoom-in-icon {
    display: block;
    width: $measure--small5;
    height: $measure--small5;
    background-color: $color-purewhite;
    color: $color-black;
    border-radius: 50%;
    border-style: none;
    transition: background-color $transition-duration--long,
        transform $transition-duration--long;

    span {
        height: 100%;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &:before {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;
    }

    &:focus {
        background: $color-nearblack;
        color: $color-purewhite;
    }

    &:focus,
    &:active {
        @include anchor-default-outline($color-outline);
    }

    &:hover,
    &:active {
        @include media('hover') {
            transform: scale(1.2);
            background-color: $color-red50;
            color: $color-purewhite;
            background-image: none;
        }
    }

    &:visited {
        color: $color-black;
    }

    &:active,
    &:hover,
    &:focus,
    &:visited {
        border-bottom-style: none;
    }
}

article {
    a {     
        color: $color-purewhite;
        text-decoration-color: $color-red50;
        text-decoration-line: underline;
        text-decoration-thickness: $border-width-default;
        font-weight: $font-weight-bold;

        &:hover {
            text-decoration-thickness: $border-width-thick;
            text-decoration-color: $color-purewhite;
        }

        &:focus {
            background-color: $color-purewhite;
            color: $color-black;
            @include outline-properties($color-purewhite);   
        }
        
        &:active {
            background-color: $color-purewhite;
            color: $color-black;
        }
    }
}
