@use '../../abstracts' as *;
@use '../../base' as *;
@use '../../layout' as *;

.banner--default {
    margin-top: $measure--basic;
    overflow: hidden;
    
    .banner__text {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: $measure--half;
        margin-bottom: $measure--half;
    }

    .banner__heading {
        width: fit-content;
        
    }

    .standfirst {
        text-align: center;
    }

    &.banner--default--dark {
        .banner__svg--left {
            display: flex;       
            justify-content: flex-start;
            
            svg {
                height: 100%;
            }
        }
        
        .banner__svg--right {
            display: flex;       
            justify-content: flex-end;

            svg {
                height: 100%;
            }
            
        }
    }

    &.banner--default--vi {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;

        .banner__svg {
            width: 100%;

            svg {
                height: 100%;
                width: 100%;
                display: block;
            }

            @include media('<=small') {
                position: absolute;
                width: 100%;
                height: 100%;
            }
        }

        .banner__text {
            position: absolute;

            @include media('<=small') {
                position: relative;
                margin: $measure--medium3 $measure--basic;
            }
        }
    }
}
