// Generic styles
main > .news {
    .content {
        padding: 0;
        margin: 0;
    }

    .banner-news__label {
        display: grid;

        h1,
        .labels {
            grid-column: 1/-1;
        }
    }

    .news__container {
        @include standard-grid();
        @include media('<mobile') {
            @include default-margin();
        }

        > div:last-child {
            border-bottom: none;
        }
    }

    .news__summary {
        padding-bottom: $measure--small2;

        p {
            @include standfirst();

        }
    }

    .news__wrap {
        border-top: $border-width-hairline $border-style-solid $color-gray4;
        padding-bottom: $measure--threequarters;
        border-bottom: $border-width-hairline $border-style-solid $color-gray4;
    }

    .news__published {
        p {
            @include smalltype();

            color: $color-text--sub;
            max-width: unset;
        }
    }

    .news__body {
        blockquote {
            p {
                @include font-family('heading');
                @include gammatype();
                @include heading-font-features();

                font-weight: $font-weight-light;
                line-height: $line-height-ratio--body;
            }
        }

        .embedded-video {
            margin-top: $measure--small2;
            padding: 0;
        }

    }

    .news__social-icons {
        margin: $measure--basic 0;

        @include media('>=tablet') {
            margin: $measure--small4 0;
        }

        p {
            font-weight: $font-weight-medium;
            margin-right: $measure--basic;
        }

        ul {
            margin-left: 0;
            margin-right: 0;
            display: flex;
            flex-direction: row;
        }

        li {
            list-style: none;
            margin-right: $measure--half;
        }
    }

}

.social-icons {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    list-style-type: none;
    margin-bottom: $measure--small2;
    margin-left: 0;
    margin-right: 0;
    margin-top: $measure--small2;
    max-width: none;

    li {
        margin-right: $measure--threequarters;
        margin-top: 0;

        &.copyLink {
            margin-right: 0;
        }
    }
}

.social-icon {
    @include anchor-reset();
    @include zoom-in-icon();
}
