@use "../abstracts" as *;
@use "../base" as *;

main {
  @include media(">=tablet", "<small") {
    > .standard-content {
      @include standard-grid();
      grid-column: span 24;

      .banner {
        grid-column: span 24;
      }

      .content {
        grid-column: 4 / span 18;
      }
    }
  }

  .standard-content {
    .content {
      overflow: hidden;
    }
  }
}

// Desktop-specific layout
main {
  @include media(">=small") {
    > .standard-content {
      @include standard-grid();

      grid-column: span 24;

      .banner {
        grid-column: span 24;
      }

      .content {
        grid-column: 6 / span 14;
      }
    }
  }

  @include media(">=desktop") {
    > .standard-content {
      .content {
        grid-column: 7 / span 12;
      }
    }
  }
}

// Generic styles
.standard-content {
  padding-bottom: $measure--small2;

  .content {
    @include standard-box();
    @include media(">=tablet") {
      margin: 0;
    }
  }
}
