@use 'variables' as *;
@use 'colors' as *;
@use 'measurements' as *;
@use 'breakpoints' as *;
@use 'anchor-mixins' as *;
@use 'palette' as *;
// @use '../patterns/anchors/anchors ' as *;

@mixin screenreader-only {
    border: 0;
    clip: rect(0, 0, 0, 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
}

@mixin veil() {
    background-color: rgba(30, 30, 30, 0.3);
    bottom: 0;
    display: none;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    z-index: z-index('veil');

    &[data-on='true'] {
        display: block;
    }

    @keyframes veilFadeIn {
        0% {
            background-color: rgba(30, 30, 30, 0);
            visibility: visible;
        }

        100% {
            background-color: rgba(30, 30, 30, 0.3);
        }
    }

    @keyframes veilFadeOut {
        0% {
            background-color: rgba(30, 30, 30, 0.3);
            visibility: visible;
        }

        100% {
            background-color: rgba(30, 30, 30, 0);
        }
    }
}

@mixin menu-animation-slideOpen {
    animation-duration: 0.4s;
    animation-name: slideOpen;

    @keyframes slideOpen {
        0% {
            max-height: 0;
            visibility: visible;
        }

        100% {
            max-height: 100vh;
        }
    }
}

@mixin menu-animation-slideClose {
    animation-duration: 0.4s;
    animation-name: slideClose;

    @keyframes slideClose {
        0% {
            max-height: 100vh;
            visibility: visible;
        }

        100% {
            max-height: 0;
            visibility: hidden;
        }
    }
}


////
// this button mixin is used on: image-carousel
////
@mixin control-button($color1: $color-red50) {
    color: $color1;
    border: $border-width-hairline solid $color1;
    padding: $measure--threequarters;
    font-weight: $font-weight-normal;

    &:hover {
        @include media('hover') {
            color: $color-purewhite;
            background: $color1;
        }
    }

    &:focus,
    &:active {
        color: $color-purewhite;
        background: $color1;
        @include anchor-default-outline($color-outline);
    }

    &:disabled {
        color: $color-gray7;
        background: $color-purewhite;
        border: none;
        opacity: 0.7;
    }
}
