@use '../../abstracts' as *;
@use '../../base' as *;
@use '../anchors' as *;

.home-banner--video {
    margin: 0;
    position: relative;
    top: 0;
    width: 100%;
    height: 100%;

    video {
        background-size: cover;
    overflow: hidden;
    object-fit: cover;

    @include media('<small') {
        min-height: 450px;
    }

    @include media('>=small') {
        min-height: 600px;
    }

    @include media('>=desktop') {
        max-height: 70vh;
    }
    }
    

    .banner__video__button__wrap {
        max-width: $max-line-length;
        @include standard-grid;
        z-index: z-index('above');
        position: absolute;
        top: 0;
        height: 100%;
        right: 0;
        left: 0;
        align-items: end;

        @include media('>=small') {
            @include standard-box();
        }

        @include media('<small') {
            @include default-margin();
        }
    }

    .banner__video__button {
        border-radius: 50%;
        border: $border-width-default solid $color-purewhite;
        color: $color-purewhite;
        height: $measure--medium3; // 3rem
        width: $measure--medium3;
        background-color: $color-gray10;
        z-index: z-index('veil'); // 3
        padding: $measure--half; // 0.5rem
        transition: all 0.2s ease-in-out;
        grid-column: -2/ -1;
        margin-bottom: $measure--small4;

        &:hover {
            @include media('hover') {
                background-color: $color-brand-primary;
                transform: scale(1.2);
            }
        }

        &:focus {
            background-color: $color-brand-primary;
        }

        &:focus,
        &:active {
            outline: $border-width-default $border-style-solid $color-purewhite;
            outline-offset: 3px;
        }

        .button__img--pause,
        .button__img--play {
            margin: auto;
            height: 100%;
        }

        .button__img--pause {
            display: block;
        }
        .button__img--play {
            display: none;
        }

        &.play {
            .button__img--pause {
                display: none;
            }
            .button__img--play {
                display: block;
            }
        }
    }

    .home-banner__content {
        position: absolute;
        top: 50%;
        left: 0;
        color: $color-purewhite;
        width: 100%;
        z-index: z-index('inpage');
        @include default-padding();
        width: 100%;
        max-width: inherit;
        display: flex;
        justify-content: center;

    }

    .home-banner__title {
            width: fit-content;

        

        /*
        p {
            text-shadow: 0px 0px 5px rgba(0, 0, 0, 0.65);
        }

        p,
        ul {
            text-align: left;
        }
            */

        @include media('>=tablet') {
            p,
            ul {
                text-align: center;
            }
        }
    }
}