@use '../../abstracts' as *;
@use '../../base' as *;
// @use '../../layout' as *;

.tiles__wrap {
    margin:$measure--large1 0;
}
.tile {
    overflow: hidden;
    border-radius: $measure--half;
    text-align: center;
    color: $color-purewhite;
    background-color: $color-gray9;
    line-height: $line-height-ratio--body;
}

.tile__heading {
    background-color: $color-red50;
    color: $color-purewhite;
    font-weight: 600;
    padding:$measure--basic;
    @include h3style();
    margin:0;

    &--alt {
        color: $color-red50;
        background-color: $color-purewhite;
    }
}

.tile__details {
    padding:$measure--small1 ;
}

.tile__text {
    text-align: left;
    margin:0 $measure--small1;
    padding:$measure--small1 0;
    border-top: $border-width-hairline $border-style-solid  $color-ford-grey;;

}