////
/// Figure
/// @group patterns
////

@use "../abstracts" as *;
@use "../base" as *;


figure figcaption,
.figcaption-like {
    @include smalltype();
    color: $color-text--caption;
    font-weight: $font-weight-book;
    line-height: $measure--small1;
    margin-top: $measure--quarter;
}

.figcaption__credit {
    display: block;
}
