@use '../../abstracts' as *;
@use '../../base' as *;
@use '../anchors' as *;

.js .slider__wrapper { 
    margin-bottom: $measure--medium7;
    position:relative;
    overflow: hidden;

    .related-content {
        max-width: inherit;
        margin: $measure--basic 0;

        .card--homepage { //li
            min-height: $measure--huge5 * 2;
            padding-bottom: $measure--medium9;

            @include media('<=small') {
                padding-bottom: 0;
            }

            .card__details__heading {
                @include thetatype();
    
                & { 
                    font-weight: $font-weight-bold;
                    text-transform: uppercase;
                    font-style: italic;
                }
            }
    
            .card__details__text {
                display: flex;
                flex-direction: row;
            }
    
            .card__container {
                display: flex;
                flex-direction: row;
                @include standard-box();

                @include media('<=tablet') {
                    flex-direction: column-reverse;

                }
            }

            .card__col {
                flex-basis: 50%;

                &:first-child {
                    background-color: $color-dark-bg;
                    z-index: z-index('above');

                    @include media('<=small') {
                        padding-right: $measure--basic;
                    }
                    
                    @include media('<=tablet') {
                        padding-right: 0;
                    }
                }

                &:last-child {
                    display: flex;
                    justify-content: center;
                }
            }
    
            .card__svg-shape {
                position: relative;
                display: block;
                height: $measure--medium11;
                z-index: z-index('above');

                @include media('<=tablet') {
                    position: absolute;
                    left: 0;
                }
    
                svg {
                    width: 100%;
                    height: 100%;
                }
            }
    
            .card__image {
    
                img {
                    position: absolute;
                    right: 0;
                    height: 100%;

                    @include media('<=tablet') {
                        position: relative;
                    }
                }
            }
        }
        
        &+.slider__controls {
            position: absolute;
            left: 0;
            right: 0;
            bottom: 0;
            @include standard-box();

            @include media('<=small') {
                position: relative;
            }
        }
    }
}